import ProductCard from "../Components/ProductCard";
import "./sn.css";  // Since it's in the same directory


const Products = () => {
  const products = [
    { title: "Laptops", description: "High Performace Laptops", image: "data:image/webp;base64," },
    { title: "Mobiles", description: "All Budget Mobiles", image: "data:image/webp;base64," },
    { title: "Bluetooth Earphones", description: "Quality Audio Products", image: "https://encrypted-tbn1.gstatic.com/shopping?q=tbn:ANd9GcQHrFLvNEET2ChhGYYljy4x3ZD7k_LIjJl32UW-xFq0K08EZG0QcHggQA1xjIkxJmP0dNXd7mcfJauglGJJK9f37DynaqT7xNv8MqKURFP1oOYPS419D7mtlb811t45DB8CjNn4RSeB3g&usqp=CAc" },
  ];

  return (
    <div className="container">
      <h1>Our Products</h1>
      <div className="card-container">
        {products.map((prod, index) => (
          <ProductCard key={index} {...prod} />
        ))}
      </div>
    </div>
  );
};

export default Products;